.navbar{
    width: 100%;
    min-height: 6vh;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #eee;
    transition: all .7s ease-in;
  }
  .scrolled{
    position: fixed;
    top: 0;
    left: 0;
    background-color: lightblue;
  }
  .logo img{
    width: 50px;
  }
  .navigation {
    ul {
      box-sizing: border-box;
      list-style-type: none;
      display: flex;
      min-height: 6vh;
      justify-content: flex-end;
      align-items: center;
  
      li{
        text-align: center;
        padding: .5em;
        cursor: pointer;
        &:hover{
          background-color: plum;
          color: white;
        }
      }
  
      li a{
        text-decoration: none;
        color: inherit;
      }
  
    }
  }